import {customElement} from '@here/hds-base';
import {AccordionItem} from '@here/hds-accordion';
import '@here/hds-avatar';
import {IconStyle} from '@here/hds-icon';
import {html, nothing, PropertyValues} from 'lit';
import {property, query} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {ifDefined} from 'lit/directives/if-defined.js';
import {styles} from './hds-vertical-navigation-item.css.js';
import {VerticalNavigationSubList} from './hds-vertical-navigation-sub-list.js';
import {
  VerticalNavigationMode,
  VerticalNavigationSelectPayload,
} from './hds-vertical-navigation.js';

/**
 * @slot default Content for a `hds-vertical-navigation-sub-list` structure
 * @event {CustomEvent<{verticalNavItemId: string}>} select Fired when the item has been selected `VerticalNavigationSelectPayload`
 */
@customElement('hds-vertical-navigation-item')
export class VerticalNavigationItem extends AccordionItem {
  static itemIdCounter = 0;
  @property({type: Boolean, attribute: false}) hasSubList = false;
  @property({type: Boolean, attribute: false}) chevron = false;
  @property({type: String, attribute: false}) navMode: VerticalNavigationMode = 'expanded';
  @property({type: String, reflect: true, attribute: 'avatar-url'}) avatarUrl = '';
  @property({type: String, reflect: true, attribute: 'avatar-name'}) avatarName = '';
  @property({type: String, attribute: 'icon-style'}) iconStyle: IconStyle = 'solid';
  @property({type: Boolean, attribute: true, reflect: true}) selected = false;
  @query('slot:not([name])') protected slotElement!: HTMLSlotElement;

  static get styles(): any {
    return [super.styles, styles];
  }

  protected firstUpdated(_changedProperties: PropertyValues) {
    super.firstUpdated(_changedProperties);
    this.id = this.id ?? `hds-vertical-navigation-item-${++VerticalNavigationItem.itemIdCounter}`;
  }

  private _slotChange() {
    let hasSubList = false;

    this.slotElement.assignedElements().forEach(e => {
      if (e instanceof VerticalNavigationSubList) {
        hasSubList = true;
      } else {
        e.slot = 'headline';
      }
    });

    this.hasSubList = hasSubList;
  }

  protected updated(_changedProperties: PropertyValues) {
    super.updated(_changedProperties);
    if (!this.disabled && this.selected && _changedProperties.has('selected')) {
      this.dispatchEvent(
        new CustomEvent<VerticalNavigationSelectPayload>('select', {
          detail: {verticalNavItemId: this.id, verticalNavItemIndex: this.index},
          bubbles: true,
          composed: true,
        })
      );
    }
  }

  protected renderIcon() {
    let icon: any = nothing;
    if (this.icon) {
      icon = html`
        <hds-icon
          class="icon"
          name="${this.icon}"
          category="${this.iconCategory}"
          icon-style="${this.iconStyle}"
          size="24px"
        ></hds-icon>
      `;
    } else if (this.avatarUrl || this.avatarName) {
      icon = html` <hds-avatar
        size="medium"
        name="${ifDefined(this.avatarName)}"
        image-url="${ifDefined(this.avatarUrl)}"
      ></hds-avatar>`;
    }
    return icon;
  }

  protected render() {
    const classes = {
      'has-sub-list': this.hasSubList,
      'has-chevron': this.chevron,
      'has-avatar': this.avatarUrl || this.avatarName,
      'nav-expanded': this.navMode === 'expanded',
      'nav-collapsed': this.navMode === 'collapsed',
      selected: this.selected,
    };

    return html`
      <div
        class="-hds-vertical-navigation-item ${classMap(classes)}"
        aria-selected="${ifDefined(this.selected)}"
        tabindex="0"
        @slotchange="${this._slotChange}"
        @click="${this._handleClick}"
      >
        ${super.render()}
      </div>
    `;
  }

  private async _handleClick(e: Event) {
    e.stopPropagation();
    if (!this.disabled) this.selected = true;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-vertical-navigation-item': VerticalNavigationItem;
  }
}
